
























import Vue from 'vue';
import Component from 'vue-class-component';
import 'vue-material/dist/vue-material.min.css';
import 'vue-material/dist/theme/default.css';
import WorkerService from "@/service/worker/WorkerService";
import {Watch} from "vue-property-decorator";
import NotificationServcice from "@/service/motification/NotificationServcice";

@Component({})
export default class App extends Vue {

    private menuVisible: boolean = false;
    private enableNotification: boolean = false;

    private notificationService?: NotificationServcice;

    private created() {
        let workerService: WorkerService = new WorkerService(this.$store);
        this.notificationService = new NotificationServcice(workerService);

    }

    private data() {
        return {
            menuVisible: false,
            enableNotification: false,
        };
    }

    @Watch("enableNotification")
    private onNotificationSwitchToggle() {

        if (this.enableNotification && this.notificationService != null) {
            this.notificationService.askPermission()
                .then(value => {
                    console.log("Mamy zgodna notyfikacje");

                    if (this.notificationService)
                        this.notificationService.subscribeNotifications()
                            .then(value1 => {
                                console.log("Mamy subskrypcje pushy", value1);

                            })
                            .catch(reason => {
                                console.log("Dupa", reason);
                            })
                })
                .catch(reason => {
                    console.log("Dupa", reason)

                })
        }
    }

}
