import WorkerService from "@/service/worker/WorkerService";

export default class NotificationServcice {

    workerService?: WorkerService;

    constructor(workerService: WorkerService) {
        this.workerService = workerService;
    }


    public get isAvailable(): boolean {
        return 'PushManager' in window && 'serviceWorker' in navigator && this.workerService != null && this.workerService.isServiceWorkerSupported;
    }

    public askPermission(): Promise<any> {
        return new Promise(function (resolve, reject) {
            const permissionResult = Notification.requestPermission();

            if (permissionResult) {
                return permissionResult.then(resolve, reject);
            }
        })
            .then(function (permissionResult) {
                if (permissionResult !== 'granted') {
                    throw new Error('We weren\'t granted permission.');
                }
            });
    }

    private urlBase64ToUint8Array(base64String: string): Uint8Array {
        const padding = '='.repeat((4 - base64String.length % 4) % 4);
        const base64 = (base64String + padding)
            .replace(/\-/g, '+')
            .replace(/_/g, '/')
        ;
        const rawData = window.atob(base64);
        return Uint8Array.from([...rawData].map((char) => char.charCodeAt(0)));
    }

    public subscribeNotifications(): Promise<PushSubscription> {
        const subscribeOptions:PushSubscriptionOptions = {
            userVisibleOnly: true,
            applicationServerKey: this.urlBase64ToUint8Array(
                'BJN_3yvgkDrx-mgepYurxen8N-0W4y3YVOjqkHPkZyyvjnr_1GdkZkuq_LJO7HvBNCv-OieyyHXcVfCeCC1QCQU'
            )
        };

        if (this.workerService != null && this.workerService.serviceWorkerRegistration != null) {

            return this.workerService.serviceWorkerRegistration.pushManager.subscribe(subscribeOptions);
        } else {
            return new Promise(((resolve, reject) => {
                reject();
            }))
        }
    }

    /*
    Public Key:
BJN_3yvgkDrx-mgepYurxen8N-0W4y3YVOjqkHPkZyyvjnr_1GdkZkuq_LJO7HvBNCv-OieyyHXcVfCeCC1QCQU

Private Key:
2UC4o3tDXi2SgPR5ZILwz14w14drzZkJe9HMY4zifIg

     */
}