export default class WorkerService {

    public serviceWorkerRegistration?: ServiceWorkerRegistration;

    constructor(store: any) {
        this.installWorker();
    }


    public get isServiceWorkerSupported(): boolean {
        return 'serviceWorker' in navigator;
    }

    private installWorker() {
        if (this.isServiceWorkerSupported)
            navigator.serviceWorker.register('/service.js').then(
                value => {
                    this.onRegisterSuccess(value)
                },
                reason => {
                    this.onRegisterError(reason)
                }
            );
    }

    private onRegisterSuccess(registration: ServiceWorkerRegistration) {
        this.serviceWorkerRegistration = registration;
        console.log('ServiceWorker registration successful with scope: ', registration);

    }

    private onRegisterError(err: any) {
        console.log('ServiceWorker registration failed: ', err);

    }

}
